.projets-section {
    padding: 2rem;
    background: #f0f8ff; /* bleu clair pour colorer */
    border-radius: 10px;
    margin-bottom: 2rem;
  }
  
  .projets-section h3 {
    color: #333;
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .projets {
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
    justify-content: center;
  }
  
  .projet-link {
    display: inline-block;
    padding: 0.6rem 1rem;
    background: #007bff;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-weight: 500;
    transition: transform 0.3s, background 0.3s;
    opacity: 0;
    transform: translateY(20px);
    animation: fadeInUp 0.5s forwards;
  }
  
  .projet-link:hover {
    background: #0056b3;
    transform: translateY(-3px);
  }
  
  /* Animation pour chaque projet */
  @keyframes fadeInUp {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  